// @ts-nocheck
import { html, TemplateResult, CSSResultGroup } from "lit";
import { property } from "lit/decorators.js";
import style_sensor from "./sensor.styles";
import { MyElement } from "./element";

interface SensorConfig {
  name?: string;
  class?: string;
  prefix?: string;
  force_integer?: boolean;
  unit?: string;
  label?: string;
  disabled_if?: any;
  [key: string]: any;
}

export class Sensor extends MyElement {
  static override styles: CSSResultGroup = style_sensor;

  @property({ type: Object })
  override conf?: SensorConfig;

  constructor() {
    super();
  }

  override connectedCallback(): void {
    super.connectedCallback();
    this.stateOn = this.stateObj?.state === 'on';
  }

  override updated(): void {
    if (this.stateObj) {
      this.stateOn = this.stateObj.state === 'on';
    }
  }

  protected override _render(_style: string = ''): TemplateResult {
    if (!this.conf) {
      return html``;
    }

    const sclass = this.conf.class || '';
    let value: string | number = '';
    let unit = '';

    if (this.stateObj) {
      value = this.conf.label || this.stateObj.state;
      unit = this.conf.unit || this.stateObj.attributes?.unit_of_measurement || '';

      if (this.conf.prefix) {
        value = this.conf.prefix + value;
      }

      if (this.conf.force_integer && typeof value === 'string') {
        const numValue = parseFloat(value);
        if (!isNaN(numValue)) {
          value = Math.round(numValue).toString();
        }
      }
    }

    const style = this.get_style('elt.css');
    
    return html`
      <style>
        .sensor {
          background-color: rgba(${this.c},${this.alpha});
        }
      </style>
      <div class="sensor ${sclass}" style="${style}">
        <span class="value">${value}</span>
        ${unit ? html`<span class="unit">${unit}</span>` : ''}
      </div>
    `;
  }

  override render(): TemplateResult {
    const disabled = this.conf?.disabled_if ? 
      this.evaluateDisabledCondition(this.conf.disabled_if) : false;

    if (disabled) {
      return html``;
    }

    return this._render();
  }
}
